import * as React from "react";
import { Link, graphql } from "gatsby";
import Layout from "../../components/layout";
import Seo from "../../components/seo";

//graphQL Tag Function will automatically pass the result of this GraphQL query into the component props as "data"
export const blogQuery = graphql`
  query {
    allMdx(sort: { frontmatter: { date: DESC } }) {
      nodes {
        id
        frontmatter {
          title
          date(formatString: "D MMM yyyy")
          isoDate: date(formatString: "yyyy-MM-DD")
          slug
        }
        excerpt
      }
    }
  }
`;

const BlogPage = ({ data }) => {
  return (
    <Layout pageTitle="Blog">
      {data.allMdx.nodes.map((node) => (
        <article key={node.id}>
          <h2>
            <Link to={`/blog/${node.frontmatter.slug}`}>
              {node.frontmatter.title}
            </Link>
          </h2>
          <p>
            <time dateTime={node.frontmatter.isoDate}>
              {node.frontmatter.date}
            </time>
          </p>
          <p>{node.excerpt}</p>
        </article>
      ))}
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Blog"
    description="Henry Chong's personal blog, with writing about adventures in the tech industry and beyond."
  />
);

export default BlogPage;
